import { useState } from 'react';
import classNames from 'classnames';
import { CloseOutlined, MenuOutlined } from '@ant-design/icons';
import styles from './styles.module.scss';

export default function Nav() {
  const [activeItem, setActiveItem] = useState('#home');
  const [verticalVisible, setVerticalVisible] = useState(false);

  const navItems = [
    { text: '首页', toEl: '#home' },
    { text: '关于', toEl: '#about' },
    { text: '项目', toEl: '#project' },
    { text: 'Contact Me!', toEl: '#contact' },
  ];

  const scroll = (toEl) => {
    const $toEl = document.querySelector(toEl);
    if ($toEl) {
      setActiveItem(toEl);
      $toEl.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div>
      <nav className={styles.nav}>
        <div className={styles.title}>
          <span>Chris</span>
        </div>

        {/* 横版 nav */}
        <ul className={styles.horizontal}>
          {navItems.map((nav) => (
            <li
              key={nav.toEl}
              className={classNames({
                [styles.active]: nav.toEl === activeItem,
              })}
              onClick={() => scroll(nav.toEl)}>
              {nav.text}
            </li>
          ))}

          {/* 缩小版菜单栏 */}
          <li
            className={styles.navBtn}
            onClick={() => setVerticalVisible(!verticalVisible)}>
            {verticalVisible ? <CloseOutlined /> : <MenuOutlined />}
          </li>
        </ul>

        {/* 竖版 nav */}
        {verticalVisible && (
          <ul className={styles.vertical}>
            {navItems.map((nav) => (
              <li
                key={nav.toEl}
                className={classNames({
                  [styles.active]: nav.toEl === activeItem,
                })}
                onClick={() => {
                  setVerticalVisible(false);
                  scroll(nav.toEl);
                }}>
                {nav.text}
              </li>
            ))}
          </ul>
        )}
      </nav>
    </div>
  );
}
